import React from 'react'
import {
    TableCell,
    OutlinedInput,
    TableSortLabel,
    Toolbar,
    Grid,
    Link,
    Select as MuiSelect,
    MenuItem
} from '@material-ui/core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faSort,
    faSortUp,
    faSortDown,
    faPlus
} from '@fortawesome/free-solid-svg-icons'

const SortingIcon = ({ direction }: any) =>
  direction === 'asc' ? (
    <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortUp} />
  ) : (
    <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortDown} />
  );

const renderSortable = ({ column, onSort, children, direction }: any) => {
  if (column.name === 'action_delete') {
      return null
  } else {
    if (
        column.sortingEnabled !== undefined &&
        column.sortingEnabled !== null &&
        column.sortingEnabled === false
        ) {
        return <span>{column.title}</span>;
        } else {
        return (
            <TableSortLabel hideSortIcon={true} onClick={onSort}>
            {children}
            {direction ? (
                <SortingIcon direction={direction} />
            ) : (
                <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSort} />
            )}
            </TableSortLabel>
        );
    }
  }
}

const Select = ({ items, ...restProps  }: any) => {
    return (
        <MuiSelect
            variant='outlined'
            style={{ width: '100%' }}
            renderValue={(selected: any) => {
                if (selected != null) {
                    const value: any = items.find((item: any) => {
                        return item.value === selected;
                    });
                    if (value) {
                        return value.label;
                    } else {
                        return '——';
                    }
                }
                return '——';
            }}
            {...restProps}
        >
            {items.map((item: any, i: number) => (
                <MenuItem key={i} value={item.value}>
                    {item.label}
                </MenuItem>
            ))}
        </MuiSelect>
    );
};

const groupStatus = [
    {
        label: 'Active',
        value: 'active'
    },
    {
        label: 'Deactivated',
        value: 'deactivated'
    },
    {
        label: 'Awaiting activation',
        value: 'awaiting_activation'
    }
]
const userStatus = [
    {
        label: 'Active',
        value: 'active'
    },
    {
        label: 'Deactivated',
        value: 'deactivated'
    },
    {
        label: 'Locked',
        value: 'locked'
    },
    {
        label: 'Awaiting activation',
        value: 'awaiting_activation'
    }
]

const FilterCell = ({ filter, onFilter, classes, column }: any) => {
    if (column.name === 'user_status' || column.name === 'role_status') {
        return (
            <TableCell classes={classes} style={{paddingTop: '5px'}}>
                <Select
                    onChange={e => {onFilter(e.target.value ? { value: e.target.value } : null)}}
                    name={'status'}
                    items={column.name === 'user_status' ? userStatus : groupStatus}
                    id={'status'}
                    value={filter ? filter.value : ''}
                />
            </TableCell>
        )
    } else if (column.name === 'action_delete') {
        return (
            <TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}></TableCell>
        )
    } else {
        return (
            <TableCell style={{ padding: '0px 10px 8px 10px' }} classes={classes}>
                <OutlinedInput
                    classes={classes}
                    style={{ width: '100%' }}
                    value={filter ? filter.value : ''}
                    labelWidth={0}
                    onChange={e => onFilter(e.target.value ? { value: e.target.value } : null)}
                    placeholder=""
                />
            </TableCell>
        );
    }

    return <></>
}


const renderSearchPanel = ({value, onValueChange}: any, add, buttonLabel) => {
    return (
        <Grid
            container
            justify="center"
            alignItems="center">
           <Grid item xs={9}>
                <OutlinedInput
                    style={{ width: '100%' }}
                    value={value ? value : ''}
                    labelWidth={0}
                    onChange={e => onValueChange(e.target.value ? e.target.value : '')}
                    placeholder="Search Roles"
                />
            </Grid>
            <Grid item xs={3}>
                <FontAwesomeIcon icon={faPlus} style={{color: '#3AB77D', marginLeft: 20, marginRight: 10}} />
                <Link onClick={add}>{buttonLabel}</Link>
            </Grid>
        </Grid>
    )
}

const DefaultTableToolbar = ({ children }: any) => {
    return <Toolbar disableGutters={true} children={children} />;
};

export {
    renderSortable,
    renderSearchPanel,
    FilterCell,
    DefaultTableToolbar
}