import React from 'react'

// Devexpress imports
import {
    Grid,
    Table,
    TableHeaderRow,
    TableSelection,
    PagingPanel,
    TableFilterRow,
    Toolbar,
    SearchPanel
} from '@devexpress/dx-react-grid-material-ui'

import {
    PagingState,
    CustomPaging,
    SortingState,
    SelectionState,
    FilteringState,
    IntegratedFiltering,
    IntegratedPaging,
    IntegratedSorting,
    SearchState
} from '@devexpress/dx-react-grid'

import {
    TableCell
} from '@material-ui/core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faMinusCircle
} from '@fortawesome/free-solid-svg-icons'

import { renderSortable, FilterCell, renderSearchPanel, DefaultTableToolbar } from './SubComponents'

interface IProps {
    rows: any[]
    columns: any[]
    totalCount?: number
    currentPage?: number
    pageSize?: number
    pageSizes: number[]
    statusOptions?: any[]
    integrated?: boolean
    columnExtension?: any
    buttonLabel?: any
    activeMenu?: string
    // Evemts
    onCurrentPageChange?: (value) => void
    onFilterChange?: (value) => void
    onPageSizeChange?: (value) => void
    onSortingChange?: (value) => void
    onRowClick?: (value) => void
    handleClickAdd?: () => void
    onDeleteRow?: (e) => void
}

export const TableComponent: React.FC<IProps> = (props: IProps):JSX.Element => {
    const {
        rows,
        columns,
        totalCount,
        currentPage,
        pageSize,
        pageSizes,
        integrated,
        columnExtension,
        buttonLabel,
        activeMenu,
        // events
        // onFilterChange,
        onCurrentPageChange,
        onPageSizeChange,
        onSortingChange,
        onRowClick,
        handleClickAdd,
        onDeleteRow
    } = props

    const getRowId = row => row._id;
    const [selection, setSelection] = React.useState([]);

    const handleSelection = (e) => {
      setSelection(e);
      onRowClick && onRowClick(e);
    }

    const onFilterChange = (filter) => {
        setDefaultFilter(filter)
    }

    React.useEffect(() => {
        setDefaultFilter([])
    }, [activeMenu])
    
    const tableCell = ({ value, row, column }: any) => {
        if (column.name === 'action_delete') {
            return (
                <TableCell style={{ padding: '0px 30px 8px 10px', textAlign: 'right' }}>
    
                    <span
                        onClick={() => {
                            onDeleteRow && onDeleteRow(row)
                        }}>
    
                        <FontAwesomeIcon
                            icon={faMinusCircle}
                        />
                    </span>
    
                </TableCell>
            )
        } else {
            return (
                <TableCell>
                    {value}
                </TableCell>
            )
        }
    }

    const [defaultFilter, setDefaultFilter] = React.useState<any[]>([])

    return (
        <>
            <Grid
                getRowId={getRowId}
                rows={rows}
                columns={columns}>

                <SearchState
                    onValueChange={(e) => console.log(e)}
                />

                <FilteringState
                    filters={defaultFilter}
                    onFiltersChange={onFilterChange} />

                {
                    integrated && <IntegratedFiltering />
                }

                
                    
                <SelectionState
                    selection={selection}
                    onSelectionChange={handleSelection}
                />

                <SortingState
                    onSortingChange={onSortingChange} />

                {
                    integrated ?
                    <IntegratedSorting /> : ''
                }

                {
                    integrated ?
                    <PagingState
                        defaultCurrentPage={0}
                    /> :
                    <PagingState
                        currentPage = {currentPage}
                        pageSize = {pageSize}
                        // Events
                        onCurrentPageChange = {onCurrentPageChange}
                        onPageSizeChange = {onPageSizeChange} />
                }
               
                         
                {
                    integrated ?
                        <IntegratedPaging />
                    : <CustomPaging totalCount = {totalCount}/>
                }

                <Table cellComponent={tableCell} columnExtensions={columnExtension} />
                <TableHeaderRow showSortingControls sortLabelComponent={renderSortable} />
                <TableFilterRow cellComponent={FilterCell} />

                <Toolbar rootComponent={DefaultTableToolbar} />
                <SearchPanel inputComponent={(e) => renderSearchPanel(e, handleClickAdd, buttonLabel)} />

                <TableSelection
                    selectByRowClick
                    highlightRow
                    showSelectionColumn={false}
                />

                <PagingPanel pageSizes = {pageSizes} />
            </Grid>
        </>
    )
}